import React, { useRef, useState } from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faChevronLeft,
  faChevronRight,
} from '@fortawesome/free-solid-svg-icons';
import styled, { css } from 'styled-components';
import {breakpoints} from '../styles/GlobalStyles.js';
import { ChevronLeft, ChevronRight } from './Svg.js';


export default function Carousel({ children, slidesDesktop, slidesMobile, center }) {

    const [hovered, setHovered] = useState(false)
    const customSlider = useRef();
    const {mobile} = breakpoints

    const ArrowContainerStyles = `
      display: grid;
      height: 100%;
      align-items: center;
      justify-content: center;
      transition: all 0.2s ease-in-out;
      z-index: 3;
      pointer-events: none;
      ${mobile}{
        display: none;
      }
    `
    const ArrowButtonStyles = `
      align-self: center;
      pointer-events: auto;
      width: 32px;
      height: 32px;
      display: grid;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      background: #fff;
    `


    const settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToScroll: 1,
        slidesToShow: slidesDesktop,
        arrows: false,
        centerMode: center,
        responsive: [
          {
            breakpoint: 1200,
            settings: {
              slidesToShow: slidesDesktop,
              slidesToScroll: 1,
            },
          },
          {
            breakpoint: 980,
            settings: {
              slidesToShow: slidesMobile,
              slidesToScroll: 1,
            },
          },
        ],
    };
  
    return (
      <div 
          onMouseEnter={() => setHovered(true)} 
          onMouseLeave={() => setHovered(false)}
      >
        <div 
          onClick={() => customSlider.current.slickPrev()}
          css={css`
            position: relative;
            display: grid;
            grid-template-columns: repeat(12, 1fr);
            padding: 0 var(--spacing-05);
            ${mobile}{
              grid-template-columns: minmax(0, 1fr);
            }
          `}
        >
          <div css={css`
              ${ArrowContainerStyles}
            `}>
            <div
              onClick={() => customSlider.current.slickPrev()}
              type="button"
              css={css`
                ${ArrowButtonStyles}
                grid-column: 1/2;
              `}
            >
              <ChevronLeft css={css`
                width: var(--font-regular);
                height: var(--font-regular);
                display: block;
              `}/>
            </div>
          </div>
          <Slider 
              {...settings} 
              ref={(slider) => (customSlider.current = slider)}
              css={css`
                grid-column: 2/12;
              `}>
              {children}
          </Slider>
          <div css={css`
            ${ArrowContainerStyles}
          `}>
            <div
              onClick={() => customSlider.current.slickNext()}
              type="button"
              css={css`
                ${ArrowButtonStyles}
                grid-column: 1/2;
              `}
            >
              <ChevronRight css={css`
                width: var(--font-regular);
                height: var(--font-regular);
                display: block;
              `}/>
            </div>
          </div>
        </div>
      </div>
    );
  }
  